/* Container for dropdown and table */
.dropdown-filter-container {
    padding: 4px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @media (max-width: 768px) {
        padding: 2px;
        margin: 2px;
    }
}

.search-bar {
    width: 100%;
    padding: 5px;
    margin-bottom: 5px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;

    @media (max-width: 768px) {
        padding: 2px;
        margin-bottom: 2px;
    }
}

/* Style for the dropdown menu */
.dropdown-filter-container select {
    padding: 10px;
    margin: 10px 0;
    width: 90%;
    max-width: 300px;
    border: 1px solid #010101;
    border-radius: 4px;
    background-color: #ffffff;
    font-size: 10px;
    font-family: 'Arial', sans-serif;
    color: #333;
    cursor: pointer;
    transition: border-color 0.3s;
    height: 20px;
    
    @media (max-width: 768px) {
        padding: 5px;
        margin: 5px 0;
        width: 80%;
        max-width: 200px;
        font-size: 8px;
    }
}

.dropdown-filter-container select:focus {
    border-color: #080908;
    outline: none;
}

/* Container for the table with fixed height and scrolling */
.table-container {
    width: 100%;
    max-height: 360px;
    overflow-y: auto;
    border: 1px solid #0f0d0c;
    border-radius: 4px;
    background-color: #ffffff;
    display: block;
    
    @media (max-width: 768px) {
        max-height: 200px;
    }
}

/* Style for the table */
table {
    width: 100%;
    border-collapse: collapse; /* Ensures borders between cells are shared */
    font-family: 'Arial', sans-serif;
}

/* Style for table headers */
th {
    background-color: lightgray;
    padding: 8px;
    text-align: center;
    color: #080909;
    font-size: 12px;
    font-weight: bold;
    border: 1px solid #e0e0e0; /* Light gray border on all sides */
    
    @media (max-width: 768px) {
        padding: 4px;
        font-size: 10px;
    }
}

/* Style for table cells */
td {
    padding: 8px;
    font-size: 11px;
    text-align: center;
    color: #333;
    border: 1px solid #e0e0e0; /* Light gray border on all sides */
    
    @media (max-width: 768px) {
        padding: 4px;
        font-size: 9px;
    }
}

/* Hover effect for table rows */
tbody tr:hover {
    background-color: #f1f8e9;
}

/* Style for alternating row colors */
tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}
.column-7 {
    width: 120px; /* Adjust this value as needed */
}
.column-8 {
    width: 80px; /* Adjust this value as needed */
}
.column-3 {
    width: 100px; /* Adjust this value as needed */
}