/* Container for holding both tables in a row */
.tables-row {
    display: flex; /* Use flexbox for horizontal alignment */
    width: 100%; /* Full width of the parent container */
    gap: 10px; /* Space between the two tables */
    box-sizing: border-box; /* Ensure padding and border are included in the width */
    
    /* Responsive design for smaller screens */
    @media (max-width: 768px) {
        flex-direction: column; /* Stack tables vertically on smaller screens */
        gap: 20px; /* Increase gap between tables on smaller screens */
    }
}

/* Common styles for table containers */
.table-container32,
.table-container12 {
    flex: 1; /* Allow the container to grow and shrink as needed */
    max-height: 180px; /* Adjust this height as needed */
    padding: 08px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: auto; /* Enables vertical scrolling */
    box-sizing: border-box; /* Include padding and border in the width */
    
    /* Responsive design for smaller screens */
    @media (max-width: 768px) {
        max-height: 150px; /* Reduce max height on smaller screens */
        padding: 4px; /* Reduce padding on smaller screens */
    }
}

/* Specific styles for .table-container32 and .table-container12 */
.table-container32,
.table-container12 {
    width: 50%; /* Each container takes up 50% of the row width */
    border: 1px solid #080808;
    
    /* Responsive design for smaller screens */
    @media (max-width: 768px) {
        width: 100%; /* Each container takes up full width on smaller screens */
    }
}

/* Table styling */
.table-container32 table,
.table-container12 table {
    width: 100%; /* Full width of the container */
    border-collapse: collapse;
    font-family: 'Arial', sans-serif;
}

/* Custom scrollbar styles for WebKit browsers */
.table-container32::-webkit-scrollbar,
.table-container12::-webkit-scrollbar {
    width: 8px; /* Adjust the width of the scrollbar */
}

.table-container32::-webkit-scrollbar-track,
.table-container12::-webkit-scrollbar-track {
    background: #f1f1f1; /* Background of the scroll track */
}

.table-container32::-webkit-scrollbar-thumb,
.table-container12::-webkit-scrollbar-thumb {
    background: #888; /* Color of the scroll thumb */
    border-radius: 8px; /* Rounded corners for the scrollbar thumb */
}

.table-container32::-webkit-scrollbar-thumb:hover,
.table-container12::-webkit-scrollbar-thumb:hover {
    background: #555; /* Darker color when hovering */
}

/* Header and cell styling */
.table-container32 th, 
.table-container32 td, 
.table-container12 th, 
.table-container12 td {
    padding: 6px; /* Adjusted padding for a more compact look */
    text-align: left;
    color: #333;
    
    /* Responsive design for smaller screens */
    @media (max-width: 768px) {
        padding: 4px; /* Reduce padding on smaller screens */
        font-size: 10px; /* Reduce font size on smaller screens */
    }
}

.table-container32 th, 
.table-container12 th {
    background-color: lightgray;
    color: #0a0a0a;
    font-weight: bold;
    font-size: 11px;
    border-bottom: 2px solid #030303;
    height:4px;
    
    /* Responsive design for smaller screens */
    @media (max-width: 768px) {
        font-size: 10px; /* Reduce font size on smaller screens */
    }
}

.table-container32 tr:nth-child(even), 
.table-container12 tr:nth-child(even) {
    background-color: #f1f9f9;
}

.table-container32 tr:nth-child(odd), 
.table-container12 tr:nth-child(odd) {
    background-color: #ffffff;
}

.table-container32 tr:hover, 
.table-container12 tr:hover {
    background-color: #f1f8e9;
}

.table-container32 td strong, 
.table-container12 td strong {
    font-weight: bold;
    color: #060708;
}